import { css } from "@emotion/core"
import { graphql, Link, useStaticQuery } from "gatsby"
import React from "react"
import { rhythm } from "../utils/typography"
import layoutStyle from "./layout-css-module.module.css"

const Menu = props => <div className={layoutStyle.menu}>{props.content}</div>

export default ({ children }) => {
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  )

  //쿼리로부터 title을 받는다.
  //title, about, contact 상단 메뉴를 만든다. 각각 링크를 건다. css를 적용해준다.
  return (
    <div>
      <div className={layoutStyle.navbar}>
        <Link to={"/"}>
          <h2
            css={css`
              padding: 10px 25px;
            `}
          >
            {data.site.siteMetadata.title}{" "}
          </h2>
        </Link>
        <Link to={"/about/"}>
          <Menu content="About" />
        </Link>
        <Link to={"/work-list/"}>
          <Menu content="Works" />
        </Link>
        <Link to={"/contact/"}>
          <Menu content="Contact" />
        </Link>
        <Link to={"/fileSystem/"}>
          <Menu content="FileSystem" />
        </Link>
        <Link to={"/md-index/"}>
          <Menu content="md" />
        </Link>
      </div>
      <div
        class="contents"
        css={css`
            margin: 20px;
            color: rgb(140,140,140);
            font-family: 'Darker Grotesque', sans-serif;
            font-size: 16px;
        `}
      >
        {children}
      </div>
    </div>
  )
}
